body{margin:0;background:#f8f8f8;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.card{position:relative;width:345px;height:360px;margin:20px 0;padding:1.5rem;border:none;border-radius:0;box-shadow:0 1px 5px 0 rgba(39,45,51,0.2)}.card:hover{box-shadow:0 2px 16px 0 rgba(39,45,51,0.4);cursor:pointer}.card .card-date{font-family:Inter;color:#fff}.card .card-title{font-family:Inter;font-size:1.5rem;font-weight:500;color:#fff;word-break:break-word;overflow:hidden;display:-webkit-box;max-height:4rem;line-height:2rem;-webkit-line-clamp:2;-webkit-box-orient:vertical}.card .card-content{font-family:Inter;color:#fff;word-break:break-word;overflow:hidden;display:-webkit-box;max-height:7.5rem;line-height:1.5rem;-webkit-line-clamp:5;-webkit-box-orient:vertical}.card .button-card{position:absolute;left:1.5rem;bottom:1.5rem;border:1px solid #fff;color:#fff;background-color:transparent;font-family:Rubik}.homepage-wrapper{width:100%;padding:2% 2%;background-color:#f8f8f8}@media screen and (min-width: 768px){.homepage-wrapper{padding:2% 15%}}.homepage-wrapper .homepage-title{font-family:Inter;font-size:1.5rem;color:#d67878;font-weight:600}.homepage-wrapper .homepage-info{font-family:Inter;color:#5a6872}.homepage-wrapper .card-wrapper{display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center;align-content:center}.homepage-wrapper .button-homepage{display:block;margin:30px auto;border:2px solid #d67d67;color:#d67d67;background-color:transparent;font-family:Rubik;font-weight:600}
